.logoloop {
  position: relative;
  overflow: hidden;
  display: block;
  will-change: transform;
  --logoloop-gap: 32px;
  --logoloop-logoHeight: 28px;
}
.logoloop__track {
  display: flex;
  width: max-content;
  will-change: transform;
  user-select: none;
}
.logoloop__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  gap: var(--logoloop-gap);
  flex-shrink: 0;
  flex-grow: 0;
}
.logoloop__item {
  height: var(--logoloop-logoHeight);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: visible;
  flex: 0 0 auto;
}
.logoloop__node {
  display: inline-flex;
  align-items: center;
  height: 100%;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.logoloop__link {
  display: inline-flex;
  align-items: center;
  height: 100%;
  text-decoration: none;
  border-radius: 4px;
  transition: opacity 0.2s ease;
}
.logoloop__item img {
  height: var(--logoloop-logoHeight);
  width: auto;
  display: block;
  object-fit: contain;
  image-rendering: -webkit-optimize-contrast;
  -webkit-user-drag: none;
  pointer-events: none;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.logoloop--scale-hover {
  padding-top: calc(var(--logoloop-logoHeight) * 0.1);
  padding-bottom: calc(var(--logoloop-logoHeight) * 0.1);
}
.logoloop--scale-hover .logoloop__item:hover img,
.logoloop--scale-hover .logoloop__item:hover .logoloop__node {
  transform: scale(1.2);
  transform-origin: center center;
}
.logoloop--fade::before,
.logoloop--fade::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: clamp(24px, 8%, 120px);
  pointer-events: none;
  z-index: 1;
}
.logoloop--fade::before {
  left: 0;
  background: linear-gradient(
    to right,
    
    var(--logoloop-fadeColor, var(--logoloop-fadeColorAuto)) 0%, 
    rgba(0, 0, 0, 0) 100%
  );
}
.logoloop--fade::after {
  right: 0;
  background: linear-gradient(
    to left,
    
    var(--logoloop-fadeColor, var(--logoloop-fadeColorAuto)) 0%, 
    rgba(0, 0, 0, 0) 100%
  );
}
@media (prefers-reduced-motion: reduce) {
  .logoloop__track {
    transform: translate3d(0, 0, 0) !important;
  }
  .logoloop__item img,
  .logoloop__node {
    transition: none !important;
  }
}
